.containerFooter {
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.textItems {
    margin-left: 15px;
    color: #868686;
}

.buttonWrapper {
    display: flex;
    text-align: center;
    align-items: center;
    width: 200px;
    color: #868686;
    cursor: pointer;
}

.textClearCompleted {
    width: 120px;
    align-items: center;
    margin-right: 5px;
    color: #868686;
}

.active {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 25px;
    border: 1px solid #ecd4d4;
    border-radius: 5px;
}


